import React, { useEffect, useState, useRef } from "react";
import * as d3 from "d3";

export default props => {
  const [dataset, setDataset] = useState(generateDataset());
  const ref = useRef();

  useEffect(() => {
    let svg = d3.select(ref.current);

    var margin = { top: 10, right: 10, bottom: 50, left: 50 };
    var width = +svg.attr("width") - margin.left - margin.right;
    var height = +svg.attr("height") - margin.top - margin.bottom;

    var g = svg
      .append("g")
      .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

    // Scales
    var x = d3.scaleLinear().range([0, width]);
    var y = d3.scaleLinear().range([height, 0]);

    // Axes
    var xAxisCall = d3.axisBottom(x);
    var xAxis = g
      .append("g")
      .attr("class", "x-axis")
      .attr("transform", "translate(" + 0 + "," + height / 2 + ")");

    var yAxisCall = d3.axisLeft(y);
    var yAxis = g
      .append("g")
      .attr("class", "y-axis")
      .attr("transform", "translate(" + width / 2 + "," + 0 + ")");

    // Labels
    xAxis
      .append("text")
      .attr("class", "axis-title")
      .attr("transform", "translate(" + width + ", 0)")
      .attr("y", -6)
      .text("Grade Point Average");
    yAxis
      .append("text")
      .attr("class", "axis-title")
      .attr("transform", "rotate(-90)")
      .attr("y", 16)
      .text("Height / Centimeters");

    var flag = true;

    // Run this code every second...
    d3.interval(function() {
      // Flick between our two data arrays
      let data = flag ? generateDataset() : mydata.sber;
      //let data = generateDataset();
      // Update our chart with new data
      update(data);

      // Update our flag variable
      flag = !flag;
    }, 3000);

    // Run for the first time
    update(generateDataset());

    function update(data) {
      // Standard transition for our visualization
      var t = d3.transition().duration(750);

      // Update our scales
      x.domain([-10, 10]);
      y.domain([-10, 10]);

      // Update our axes
      xAxis.transition(t).call(xAxisCall);
      yAxis.transition(t).call(yAxisCall);

      // Update our circles
      var circles = g.selectAll("circle").data(data);

      circles
        .exit()
        .transition(t)
        .attr("fill-opacity", 0.1)
        .attr("cy", y(0))
        .remove();

      circles
        .transition(t)
        .attr("cx", function(d) {
          return x(d.moex);
        })
        .attr("cy", function(d) {
          return y(d.ticker);
        });

      circles
        .enter()
        .append("circle")
        .attr("cx", function(d) {
          return x(d.moex);
        })
        .attr("cy", y(0))
        .attr("r", 5)
        .attr("fill", "grey")
        .attr("fill-opacity", 0.1)
        .transition(t)
        .attr("fill-opacity", 1)
        .attr("cy", function(d) {
          return y(d.ticker);
        });
    }
  }, [dataset]);

  return (
    <svg
      width="700"
      height="600"
      //    viewBox="0 0 800 800"
      ref={ref}
    />
  );
};

let mydata = {
  sber :[
  { moex: 3.34, ticker: 6.81 },
  { moex: 4.09, ticker: 8.04 },
  { moex: 6.06, ticker: 18.7 },
  { moex: 0.57, ticker: 2.14 },
  { moex: -6.07, ticker: 12.9 },
  { moex: 8.36, ticker: 11.43 },
  { moex: -1.29, ticker: -9.04 },
  { moex: -7.32, ticker: -10.3 },
  { moex: -1.21, ticker: 8.62 }
]};


const generateDataset = () =>
  Array(100)
    .fill(0)
    .map(() => ({
      moex: Math.random() * 20 - 10,
      ticker: Math.random() * 20 - 10
    }));
    //     [
    //     Math.random() * 80 + 10,
    //     Math.random() * 35 + 10,
    // ]
